.b-request-quote {
    width: 100%;
    overflow: hidden;
    display: grid;
    padding: 3rem 2rem 0;
    @include respond-to(992) {
        grid-template-columns: 1fr 1fr;
        padding: 10rem 0;
    	height: 100vh;
        padding: 6rem 2rem;
    }
}

.b-request-quote__left {
    padding: 0 4rem;
}

.b-request-quote__left {
    margin: 0 auto;
}
