.m-loader {
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    opacity: 0;
    transition: opacity .3s ease-in;
    &.loading {
        display: flex;
        z-index: 9999;
        opacity: 1;
        background-color: #fff;
        align-items: center;
        justify-content: center;
    }
}

.m-loader__list {
    display: block;
    position:relative;
    width: 5em;
    margin: 50px auto;
}

.m-loader__item {
    list-style: none;
    display: block;
    float:left;
    width: 0.5em;
    height: 3em;
    margin: 0 0.5em 0 0;
    background: $c-yellow;
    box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, 0.0), 1px 1px 1px 0 rgba(0, 0, 0, 0.0), 1px 1px 1px 0 rgba(0, 0, 0, 0.0);
    animation: pound .5s ease-in-out infinite alternate;
    animation-delay:0.05s;
    transform-origin: center;
    
    &:nth-child(2){
        animation-delay:0.20s;
    }
    &:nth-child(3){
        animation-delay:0.35s;
    }
    &:nth-child(4){
        animation-delay:0.50s;
    }
    &:nth-child(5){
        animation-delay:0.65s;
    }
}

@keyframes pound {
    to { 
        transform: scale(1.2); 
        box-shadow: 1px 2px 3px 0 rgba(0, 0, 0, 0.65), 
                    2px 6px 12px 0 rgba(0, 0, 0, 0.5), 
                    3px 8px 15px 0 rgba(0, 0, 0, 0.45)
     }
}
