.b-navbar {
    background-color: $c-yellow;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    box-shadow: 0 2px 4px rgba(0,0,0,.5);
    
    @include respond-to(992) {
        background-color: transparent;
        box-shadow: none;
    }
    
    &__layout {
        
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 10px 20px;
        
        @include respond-to(992) {
            padding: 8px 10px 22px;
            background-color: transparent;
        }
        
        @include respond-to(1200) {
            width: 100%;
            max-width: 1300px;
            position: relative;
            z-index: 99;
            left: 50%;
            background-color: transparent;
            transform: translateX(-50%);
        }
    }
    
    &.js-nav--scroll {
        @include respond-to(992) {
            background-color: $c-yellow;
            box-shadow: 0 2px 4px rgba(0,0,0,.5);
        }
        .m-nav {
            @include respond-to(992) {
                background-color: $c-yellow;
                overflow: visible;
            }
        }
        .m-nav__item {
            @include respond-to(992) {
                padding: 15px 5px 0;
            }
        }
        .b-navbar-brand {
            position: relative;
            top: -2px;
        }
    }
}

.b-navbar-brand {
    margin-top: 5px;
    z-index: 99;
    @include respond-to(992) {
        margin-top: 28px;
        margin-left: 32px;
    }
}

.b-navbar-brand__img {
    max-width: 81px;
}
#logo-letter-b {  
    path {
        stroke: rgba(51, 51, 52, 1);
        fill: #333334;
        /* stroke-dasharray: #333334 */
        stroke-dasharray: 1800;
        opacity: 10;
        animation: animate 1.5s cubic-bezier(0,0.23,1,.1);
    }  
}

@keyframes animate {
    0% {
        opacity: 0;
        fill: none;
        stroke-dashoffset: 1800;
    }
    30% {
        opacity: 10;
        fill: none;
        stroke-dashoffset: 1800;
    }
    90% {
        fill: rgba(51, 51, 52, 0);
    }
    100% {
        opacity: 10;
        fill: rgba(51, 51, 52, 1);
        stroke-dashoffset: 0;
    }
}