.btn {
    appearance: none;
    background-color: transparent;
    border-radius: 25px;
    border: 1px solid transparent;
    box-shadow: none;
    color: $c-white;
    cursor: pointer;
    display: inline-block;
    font-family: $roboto;
    font-range: 320px 1920px;
    font-size: responsive 14px 16px;
    font-weight: bold;
    height: auto;
    letter-spacing: .08em;
    line-height: 1.2;
    margin: 0;
    padding: 11px 21px;
    text-decoration: none;
    touch-action: manipulation;
    transition: all .3s ease-in;
    vertical-align: middle;
    &:focus, &:hover{
        box-shadow: none;
        color: $c-white;
    }
    &:active, &.active{
        box-shadow: none;
        color: $c-white;
    }
    
    &-primary{
        background-color: $c-green;
        border: 1px solid $c-green;
        color: $c-white;
        &:focus, &:hover{
            background-color: $c-white;
            border: 1px solid $c-green;
            color: $c-green;
        }
    }
    
    &-transparent {
        box-shadow: $shadow-1;
        border-radius: 29px;
        border: 1px solid $c-green;
        background: transparent;
        color: #329595;
        
        &:focus, &:hover{
            background-color: $c-green;
            border: 1px solid $c-green;
            color: $c-white;
        }
    }
    &-yellow{
        background: $c-yellow;
        border: 1px solid $c-yellow;
        color: $c-white;
        &:focus, &:hover{
            border: 1px solid $c-yellow;
        }
    }
}
