.m-form {
    
    &__input,
    &__textarea,
    &__select {
        display: block;
        width: 100%;
        line-height: 50px;
        box-shadow: $shadow-3;
        border-radius: 13px;
        border: 1px solid $c-green;
        background-color: $c-white;
        color: $c-tautara;
        font-family: $roboto;
        font-size: 14px;
        font-weight: 400;
        padding-left: 20px;
        height: 54px;
        
        &:focus {
            outline: none;
        }
    }
    
    &__fieldset {
        margin-bottom: 1rem;
    }
    
    &__fieldset--privacy-policy {
        label {
            display: flex;
            max-width: 320px;
            margin: 0 auto;
        }
        .m-form__validation {
            border: 0 !important;
        }
        input {
            position: relative;
            top: 2px;
            @include respond-to(1200) {
                left: -4px;
                width: 5px;
                height: 5px;
            }
        }
        p {
            color: $darkgray;
            font-family: $roboto;
            font-size: 10px;
            font-weight: 400;
            line-height: 13px;
            text-align: left;
            margin-left: 13px;
            margin-bottom: 10px;
        }
        a {
            color: $c-green;
            font-family: $roboto;
            font-size: 10px;
            text-decoration: underline;
        }
    }
    
    &__fieldset--fancy {
        label {
            transform: translate3d(0, 43px, 0);
            z-index: 99;
            position: relative;
            float: left;
            left: 20px;
            padding: 3px;
            background-color: $c-white;
            border-radius: 25px;
        }
    }
    
    &__label {
        display: block;
    }
    
    &__select {
        appearance: none;
        // background: transparent url(http://www.regenbogen.ag/fileadmin/usr/rb_sprite_dropdown.png) no-repeat right top;
        cursor: pointer;
        width: 100%;
        & ~ label {
            transform: translate3d(0, 40px, 0);
        }
    }
    
    &__textarea {
        resize: none;
    }
    
    &__footer {
        margin-top: 33px;
    }
    
    &__validation {
        font-size: 14px;
        color: red;
        opacity: 0;
        transition: opacity .3s ease;
        &.error {
            opacity: 1;
        }
    }
    
    &__btn:focus {
        outline: none;
    }
    
    %controller {
        position: relative;
        cursor: pointer;
        padding: 1em;
        
        &::selection {
            background: transparent;
        }
        
        input + span {
            background: white;
            content: "";
            display: inline-block;
            margin: 0 .5em 0 0;
            padding: 0;
            vertical-align: middle;
            width: 2em;
            height: 2em;
            transform: translate3d(0, 0, 0);
            -webkit-backface-visibility: hidden;
            box-shadow: 0 2px 4px rgba(#000, .15);
            
            &::after {
                content: "";
                display: block;
                transform: scale(0);
                transition: transform .2s;
            }
        }
        
        input:active + span {
            box-shadow: 0 4px 8px rgba(#000, .15);
        }
        
        input:focus + span {
            box-shadow: 0 0 0 3px lightblue;
        }
        
        input:checked + span::after {
            transform: scale(1);
        }
        
        input {
            position: absolute;
            cursor: pointer;
            opacity: 0;
        }
    }
    
    .m-form__label.checkbox {
        @extend %controller;
        
        input + span {
            border-radius: 2px;
            
            &::after {
                background: url(data:image/svg+xml;base64,) no-repeat center;
                background-size: contain;
                width: 2em;
                height: 2em;
            }
        }
    }
}

.m-form--about-us {
    position: relative;
    @include respond-to(992) {
        padding: 34px 3.7rem;
    }
    .m-form__body {
        @include respond-to(1200) {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        .m-form__fieldset {
            &:nth-child(odd) {
                @include respond-to(1200) {
                    margin-right: 1.3rem;
                }
            }
            &:nth-child(even) {
                @include respond-to(1200) {
                    margin-left: 1.3rem;
                }
            }
        }
    }
}
