.b-values {
    padding: 5rem 0;
    .btn {
        margin-top: 2rem;
    }
}

.b-values__title { 
    text-align: center;
    margin-bottom: 2.5rem;
}

.b-values__layout { 
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 60px;
    padding: 0 2rem;
    @include respond-to(767) {
        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
    }
    
    @include respond-to(992) {
        grid-template-columns: 1fr 1fr;
    }
    
    @include respond-to(1200) {
        padding: 3rem 0;
    }
}

