.b-built-by {
	padding: 4rem 0 4.9rem;
	background-color: $gainsboro;
	
	&__layout {
		margin-bottom: 2rem;
		
		@include respond-to(992) {
			margin-bottom: 3rem;
			max-width: 1200px;
			margin: 0 auto;
			display: grid;
			grid-template-areas: 
			"header header"
			"article article";
		}
		
		@include respond-to(1200) {
			width: 100%;
			display: grid;
			grid-template-areas: 
			"header header header"
			"article article article";
			grid-template-columns: 1fr 1fr 1fr;
			position: relative;
			left: 15px;
		}
	}
	
	&__header {
		text-align: center;
		grid-area: header;
		margin-bottom: 1.5rem;
		@include respond-to(992) {
			margin-bottom: 3.8rem;
		}
	}
	
	&__footer {
		@include respond-to(992) {
			padding-top: 1.2rem;
		}
		@include respond-to(1200) {
			padding-top: 4.2rem;
		}
		text-align: center;
		hr {
			margin: 0 auto;
		}
		.b-built-by__footer-title {
			color: #323231;
			font-family: $roboto;
			font-size: 24px;
			font-weight: 600;
			margin: 16px auto 0;
			@include respond-to(992) {
				margin-top: 16px;
				max-width: none;
			}
		}
		p {
			color: $c-tautara;
			font-family: $roboto;
			font-size: 14px;
			font-weight: 400;
		}
		.l-btn {
			margin-top: 15px;
		}
	}
	
	h2 {
		letter-spacing: $letter-spacing--big;
	}
	
	.b-built-by__subtitle {
		color: $c-tautara;
		font-family: $roboto;
		font-size: 14px;
		padding: 0 1rem;
		@include respond-to(992) {
			margin-top: -3px;
		}
	}
	
	.m-article {
		@include respond-to(992) {
			margin-bottom: 3rem;
		}
	}
}
