.m-hamburger {
    height: 60px;
    left: 0px;
    transition-duration: 0.5s;
    width: 60px;
    position: relative;
    transform: scale(0.6);
    z-index: 99;
    
    &::hover {
        cursor: pointer;
    }
    
    @include respond-to(992) {
        display: none;
    }
    
    .icon-left {
        background-color: #212121;
        height: 8px;
        left: 0px;
        position: absolute;
        top: 30px;
        transition-duration: 0.5s;
        width: 30px;
        &::before {
            background-color: #212121;
            content: "";
            height: 8px;
            position: absolute;
            top: -20px;
            transition-duration: 0.5s;
            width: 30px;
        }
        &::after {
            background-color: #212121;
            content: "";
            height: 8px;
            position: absolute;
            top: 20px;
            transition-duration: 0.5s;
            width: 30px;
        }
        &::hover {
            cursor: pointer;
        }
        
    }
    
    .icon-right {
        background-color: #212121;
        height: 8px;
        left: 30px;
        position: absolute;
        top: 30px;
        transition-duration: 0.5s;
        width: 30px;
        &::before {
            background-color: #212121;
            content: "";
            height: 8px;
            position: absolute;
            top: -20px;
            transition-duration: 0.5s;
            width: 30px;
        }
        &::after {
            background-color: #212121;
            content: "";
            height: 8px;
            position: absolute;
            top: 20px;
            transition-duration: 0.5s;
            width: 30px;
        }
    }
    &.open {
        .icon-left {
            background: transparent;
            transition-duration: 0.5s;
            &::before {
                transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
            }
            &::after {
                transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
            }
            
        }
        .icon-right {
            transition-duration: 0.5s;
            background: transparent;
            &::before {
                transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
            }
            &::after {
                -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
                transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
            }
        }
    }    
}






