.b-header {
    background-color: $c-yellow;
    padding: 5rem 0 40px 0;
    
    @include respond-to(992) {
        background-color: transparent;
        background-size: cover;
        display: flex;
        flex-direction: column;
        padding: 0;
        position: relative;
        width: 100%;
        padding: 0.1rem 0 5rem;
    }
    
    &--about-us {
        @include respond-to(992) {
            padding: 0 0 5.3rem;
        }
    }
    
    &--space {
        @include respond-to(992) {
            padding: 0 0 8.8rem;
        }
    }
    
    .b-header__hexagon-background {
        display: none;
        @include respond-to(992) {
            display: block;
            position: absolute;
            top: -898px;
            right: -574px;
            transform: rotate(2deg);
        }
        &--left {
            @include respond-to(992) {
                right: 0;
                left: -345px;
                top: -712px;
                z-index: -1;
            }
        }
        &--single-blog {
            left: -55px;
            top: -910px;        
        }
    }
    
    .b-header__title {
        font-size: 42px;
        line-height: 1.2;
        margin-top: 12px;
        @include respond-to(992) {
            font-size: 72px;
            line-height: 66px;
        }
        &--uppercase {
            text-transform: uppercase;
        }
    }
    
    .b-header__headline {
        display: grid;
        position: relative;
        width: 100%;
        padding: 1rem 2rem 2rem;
        @include respond-to(992) {
            padding: 2.4rem 15px;
            grid-template-columns: 1fr 1fr;
        }
        .b-header__sub-title {
            color: $c-green;
            font-family: $roboto;
            font-weight: 900;
            font-size: 26px;
            letter-spacing: .056rem;
            line-height: 1.2;
            @include respond-to(992) {
                line-height: 40px;
            }
        }
    }
    
    .b-header__body {
        color: $c-tautara;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        margin: 1.25rem 0 1.8rem;
        max-width: 360px;
    }
    
    .l-container {
        @include respond-to(992) {
            grid-area: 1 / 1;
            position: relative;
        }
    }
    [intro-animate] {
        opacity: 0;
    }
    hr {
        margin: .7rem 0 1.5rem;
    }
    
    .b-header__headline--small {
        .b-header__sub-title {
            color: $c-green;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 0;
            margin-bottom: 12px;
            @include respond-to(992) {
                line-height: 20px;        
            }
        }
        .b-header__title {
            color: $c-mine-shaft;
            font-size: 36px;
            letter-spacing: -0.75px;
            line-height: 44px;
            margin-top: 0;
            text-transform: inherit;
            max-width: 330px;
        }
        hr {
            margin-top: 24px;
            margin-bottom: -2px;
        }
        .b-header__body {
            margin: 1.25rem 0 1.3rem;
        }
        .b-header__headline-img {
            display: none;
            @include respond-to(992) {
                display: block;
            }
        }
    }
    
    &.b-header--about-us {
        .b-header__headline {
            @include respond-to(992) {
                padding: .7rem 15px 3.4rem;
            }
        }
        .b-header__title {
            font-size: 58px;
            margin-bottom: 40px;
        }
        hr {
            margin: .7rem 0 1.2rem;
        }
        .b-header__body {
            margin: 1.25rem 0 0.7rem;
            a {
                color: $c-green;
                font-family: $roboto;
                font-weight: 500;
            }
        }
        .l-btn {
            color: $c-green;
            border: 1px solid $c-green;
            box-shadow: none;
            @include respond-to(1200) {
                font-size: 10px;
                text-transform: inherit;
                line-height: 24px;
                padding: 0 10px;
            }
        }
    }
    &.b-header--careers {
        .b-header__title {
            font-size: 60px;
            letter-spacing: -.066rem;
        }
    }
    &.b-header--blog {
        .b-header__headline {
            grid-template-columns: 1fr;
            padding: 2.4rem 0;
        }
        .b-header__headline-img {
            display: block;
            border-radius: 15px;
        }
    }
}
