.m-article {
	max-width: 340px;
	margin: 0 auto;
	box-shadow: $shadow-2;
	margin-bottom: 2.5rem;
	border-radius: 6px;
	display: flex;
	flex-direction: column;
	
	&__header {
		
	}
	&__header-img {
		width: 100%;
		border-top-left-radius: 6px;
		border-top-right-radius: 6px;
	}
	&__body {
		// padding: 25px 19px 20px 22px;
		padding: 25px 17px 19px 21px;
	}
	
	&__tag {
		border-radius: 29px;
		color: $c-green;
		font-family: $roboto;
		font-size: 10px;
		font-weight: 500;
		text-transform: uppercase;
		padding: 0 8px;
		display: inline-block;
		margin-right: 1rem;
		line-height: 21px;
		
		&--blue {
			color: $c-green;
			background-color: rgba(50, 149, 149, 0.2);
		}
		
		&--orange {
			color: $orange;
			background-color: rgba(248, 173, 0, 0.2);
		}
		
		&-wrapper {
			display: flex;
			justify-content: flex-start;
			margin-bottom: 22px;
		}
	}
	
	&__title {
		color: $c-tautara;
		font-family: $roboto;
		font-size: 18px;
		font-weight: 700;
		line-height: 23px;
	}
	
	&__meta {
		color: $darkgray;
		font-family: $roboto;
		font-size: 12px;
		font-weight: 400;
		margin-bottom: 8px;
		margin-top: -4px;
	}
	
	&__text {
		color: $c-tautara;
		font-family: $roboto;
		font-size: 14px;
		margin-bottom: 25px;
		line-height: 20px;
	}
	
	&__read-more {
		color: $c-green;
		font-family: $roboto;
		font-size: 12px;
		font-weight: 500;
		text-transform: uppercase;	
	}
	
}
