.b-lets-talk {
	padding: 3rem 20px;
	text-align: center;
	@include respond-to(1200) {
		padding: 5.8rem 0;
		
		position: relative;
		left: 5px;
	}
}

.b-lets-talk__layout {
	padding-bottom: 3.4rem;
	@include respond-to(1200) {
		display: grid;
		grid-template-columns: 2fr 1fr;
		background-image: url('../../img/lets-talk.svg');
		background-repeat: no-repeat;
		background-position: 100% 2px;
		max-width: 1300px;
		margin: 0 auto;
		padding-left: 45px;
		padding-bottom: 8.4rem;
	}
}

.b-lets-talk__sub-title {
	font-size: 14px;
	font-weight: 400;
	line-height: 20px;
	margin-top: 2px;
}