.m-nav {
    display: none;
    margin: auto;
    overflow: hidden;
    position: relative;
    width: 350px;
    flex-direction: column;         
    margin-right: 0;
    @include respond-to(992) {
        display: flex;
        flex-basis: auto;
    	justify-content: flex-end;
        flex-direction: row;
    }
}

.m-nav-flex {
    display: flex;
    position: absolute;
    top: 60px;
    width: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: $c-yellow;
}

.m-nav__item {
    text-align:center;
    margin: 20px;
    border: solid 1px transparent;
    transition: padding .3s ease;
    @include respond-to(992) {
        background: none;
        padding: 51px 5px 15px;
        margin: 0;
    }
    .m-nav__item-link {
        color: $c-mine-shaft;
        font-family: $roboto;
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        padding: 10px 10px;
        letter-spacing: .012rem;
        text-transform: capitalize;
    }    
    .m-nav__item-link--quote {
        border: 1px solid $c-green;
        border-radius: 50px;
        color: $c-green;
        margin: 0;
        padding: 9px 17px;
        font-size: 14px;
        text-transform: none;
        letter-spacing: 0;
        @include respond-to(1200) {
            margin-left: 96px;
            margin-right: 7px;
        }
    }
}

.m-navbar-collapse {
    -ms-flex-preferred-size: 100%;
    flex-basis: 100%;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -ms-flex-align: center;
    align-items: center;
    overflow: scroll;
    @include respond-to(mobile) {
        padding-top: 1rem;
        height: 100vh;
    }
}
