.js-swiper-team {
    width: 720px;
    height: 400px;
    overflow: hidden;
    margin: 0 auto;
}

.js-swiper-team .js-swiper-team__slides {
    display: block;
    width: 6000px;
    height: 400px;
    margin: 0;
    padding: 0;
}

.js-swiper-team .js-swiper-team__slide {
    float: left;
    list-style-type: none;
    width: 720px;
    height: 400px;
}

.js-swiper-team__progress {
    background-color: #f5f5f5;
    background-image: linear-gradient(to right, black, black);
    background-repeat: no-repeat;
    background-size: 0 100%;
    border-radius: 10px;
    display: block;
    height: 10px;
    margin: 2rem auto 0;
    max-width: 720px;
    overflow: hidden;
    width: 100%;
}

.js-swiper-team__progress-handler {
    background: $c-black;
    height: 40px;
    position: absolute;
    border-radius: 50%;
    width: 40px;
    top: -15px;
    left: 0;
	margin-left: -20px;
}

.js-swiper-team__progress-label {
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    top: 3rem;
}

.js-swiper-team__pagination {
    max-width: 720px;
    margin: 0 auto;
    position: relative;
}
