.b-blog {
    padding: 2rem 0 3.7rem;
    @include respond-to(992) {
        padding: 2rem 0 6.7rem;
    }
    &__layout {
        @include respond-to(992) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            margin: 0 auto;
            max-width: 1200px;
        }
        @include respond-to(1200) {
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 20px 0px;
        }
    }
    .l-btn {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 26px;
    }
}
