.b-blog-post {
    &__layout {
        position: relative;
        top: -200px;
        box-shadow: $shadow-2;
        
        @include respond-to(1200) {
            max-width: 1000px;
        }
        
        & > div:first-child {
            border-top-left-radius: 6px;
            border-top-right-radius: 6px;
        }
        
        & > div:last-child {
            border-bottom-left-radius: 6px;
            border-bottom-right-radius: 6px;
        }
        
        & > .l-clearfix {
            padding: 3rem 0;
            
            .col-half {
                
                &:nth-child(odd) {
                    padding-right: 30px;
                }
                
                &:nth-child(even) {
                    padding-left: 30px;
                }
            }
        }
        
        hr {
            margin: 1.6rem 0;
        }
        
        h3 {
            color: $c-tautara;
            font-size: 20px;
            font-weight: 700;
            line-height: 22px;
        }
        p {
            color: $c-tautara;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 1.2rem;
            b {
                font-weight: 700;
            }
        }
    }
    
    &__header {
        text-align: center;
        background: $c-white;
        
        @include respond-to(1200) {
            padding: 93px 7.6rem 3.3rem;
        }
        
        hr {
            margin: 1rem auto 1.3rem;
        }
        
        h2 {
            letter-spacing: -0.05rem;
            line-height: 46px;
        }
        
        p {
            font-size: 14px;
            line-height: 20px;
            
            @include respond-to(1200) {
                padding: 0 70px;
            }
        }
    }
    
    &__meta {
        font-size: 14px;
        margin-top: 3px;
        margin-bottom: 27px;
    }
    
    .col-full, 
    .col-half {
        float: left;
    }
    
    .col-full {
        width: 100%;
        padding: 0 50px;
    }
    
    .col-half {
        width: 50%;
    }
    
    .content-split {
        float: left;
        max-width: 100%;
    }
}
