.m-post-ordered {
    display: flex;
    flex-direction: column;
    text-align: center;
    box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -7px rgba(0,0,0,0.2);
    border-radius: 5px;
    min-height: 10rem;
    padding: 2rem 1rem;
    @include respond-to(992) {
        flex-direction: row;
        text-align: left;
    }
}

.m-post-ordered__left {
    align-items: center;
    display: flex;
    justify-content: center;
    min-width: 6rem;
}

.m-post-ordered__right {
    padding: 1rem;
}

.m-post-ordered__number {
    color: $c-green;
    font-size: 55px;
    line-height: 1.2;
}

.m-post-ordered__title {
    font-size: 20px;
    margin-bottom: 1rem;
}

.m-post-ordered__list-item {
    margin-bottom: 1rem;
    padding-left: 1.2rem;
    position: relative;
    text-align: left;
    
    @include respond-to(992) {
        margin-bottom: .5rem;
    }
    
    .hexagon {
        left: 0;
        position: absolute;
        top: 8px;
        
        @include respond-to(992) {
            top: 9px;
        }
        @include respond-to(1500) {
            top: 10px;
        }
    }
}