.b-footer {
    background-color: $c-yellow;
    &__layout {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 20px;
        @include respond-to(992) {
            flex-direction: row;
        }
    }
    .l-container {
        max-width: 1280px;
    }
}

.b-footer__logo {
    max-width: 5.2rem;
    margin: 0 auto 2rem;
    @include respond-to(992) {
        margin: 10px 11px;
    }
}

.b-footer__right {
    display: flex;
    flex-direction: column;
    @include respond-to(992) {
        flex-direction: row;
        margin-top: 13px;
    }
}

.b-footer__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include respond-to(992) {
        flex-direction: row;
    }
}

.b-footer__description {
    margin-bottom: 1rem;
    text-align: center;
    color: $c-tautara;
    font-family: $roboto;
    font-size: 16px;
    line-height: 16px;
    position: relative;
    
    &::after {
        @include respond-to(992) {
            content: '';
            position: absolute;
            right: 0;
            height: 65%;
            border-right: 1px solid #f8ad00;
            transform: translateY(-50%);
            top: 50%;
        }
    }
    
    @include respond-to(992) {
        margin-bottom: 0;
        padding-right: .9rem;
        text-align: right;
        align-items: center;
        font-size: 12px;
        display: flex;
    }
}

.b-footer__socials-item {
    padding: .5rem;
    color: $c-tautara;
    font-family: $roboto;
    font-size: 14px;
    line-height: 26px;
    @include respond-to(992) {
        padding-left: 1rem;
        font-size: 12px;
    }
}

.b-footer__copy-right {
    margin-bottom: 1rem;
    @include respond-to(992) {
        margin-bottom: 0;
    }
}

.b-footer__socials-link {
    color: $c-black;
}