.m-slider {
    &__img {
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
    }
    &__title {
        margin-top: 25px;
        margin-bottom: 15px;
        color: $c-tautara;
        font-family: $roboto;
        font-size: 18px;
        font-weight: 700;
        line-height: 26px;
    }
    &__text {
        color: $c-tautara;
        font-family: $roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
    }
    &__link { 
        color: $c-green;
        font-family: $roboto;
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
    }
}

.m-slider--why-us {
    .slick-slide {
        width: 250px;
        padding: 31px 22px;
        margin: 0 25px 9.8rem;
        @include respond-to(1200) {
            width: 310px;
        }
    }
}
.m-slider--team {
    .slick-slide {
        width: 250px;
        padding: 0;
        margin: 0 29px 10.3rem;
        text-align: center;
    }
    .m-slider__item {
        padding-bottom: 10px;
    }
    .m-slider__title {
        font-size: 16px;
        margin-top: 18px;
        margin-bottom: 0;
    }
    .m-slider__sub-title {
        position: relative;
        top: -4px;
        font-size: 14px;
    }
    .m-slider__link {
        position: relative;
        top: -8px;
    }
}
