.b-our-speakers {
    background-color: $gainsboro;
    padding: 3rem 0;
    
    @include respond-to(1200) {
        padding: 0 0 6rem;
    }
    
    &__layout,
    &__intro {
        padding: 0 1.3rem;
        @include respond-to(1200) {
            padding: 0;
            position: relative;
            left: 15px;
        }
    }
    
    &__layout {
        max-width: 1200px;
        margin: 0 auto;
        margin-top: 60px;
        
        @include respond-to(992) {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        
        @include respond-to(1200) {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }
    
    &__intro {
        text-align: center;
        @include respond-to(1200) {
            text-align: left;
            margin-top: -76px;
            max-width: 360px;
        }
    
        h2 {
            margin-bottom: 11px;
        }
    }
    
    &__body {
        color: $c-tautara;
        font-family: $roboto;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.003rem;
        margin-bottom: 28px;
    }
}
