.b-partnerships {
    background-color: $gainsboro;
    overflow: hidden;
    position: relative;
    padding: 3rem 1.3rem;
    @include respond-to(1200) {
        top: -62px;
        padding: 0;
    }
}

.triangle {
    display: none;
    @include respond-to(1200) {
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 100vw 350px 0;
        border-color: transparent white transparent transparent;   
        display: block;
    }
}

.b-partnerships__layout {
    max-width: 997px;
    margin: 0 auto;
    padding: 1rem 0;
    text-align: center;
    grid-gap: 40px;
    grid-template-rows: 1fr;
    grid-template-areas: 
        "intro intro"
        "tm-model teach-us"
        "fixed-projects create-your";

    @include respond-to(992) {
        grid-template-columns: 1fr 1fr;
        display: grid;
        grid-gap: 40px;
        text-align: center;
    }
    
    @include respond-to(1200) {
        background: url(../../img/partnerships-hexagone.svg) no-repeat center;
        position: relative;
        top: -221px;
        left: -30px;
        text-align: left;
        grid-gap: 60px;
        background-size: 930px 932px;
        margin-bottom: -91px;
        grid-template-areas: 
            "intro tm-model"
            "teach-us fixed-projects"
            "create-your .";
    }
    
    .m-box-hexagone {
        margin-bottom: 3rem;
        @include respond-to(1200) {
            padding: 20px;
            margin-bottom: 0;
        }
    }
    
    .m-box-hexagone__body {
        margin: 0 auto;
        @include respond-to(1200) {
            padding-right: 30px;
            margin-left: 0;
        }
    }
    
    .b-partnerships__intro {
        grid-area: intro;
        @include respond-to(992) {
            max-width: 50%;
            margin: 0 auto 3rem;
        }
        @include respond-to(1200) {
            left: -46px;
            max-width: 100%;
            min-width: 480px;
            position: relative;
            top: 78px;
        }
        p {
            font-size: 14px;
            line-height: 20px;
            @include respond-to(1200) {
                max-width: 340px;
                position: relative;
                top: 14px;
            }
        }
    }
    
    .b-partnerships__tm-model {
        grid-area: tm-model;
        @include respond-to(1200) {
            position: relative;
            top: 138px;
            left: 35px;
        }
    }
    
    .b-partnerships__teach-us {
        @include respond-to(1200) {
            position: relative;
            left: 189px;
            top: -11px;
        }
    }
    
    .b-partnerships__fixed-projects {
        grid-area: fixed-projects;
        @include respond-to(1200) {
            position: relative;
            top: 232px;
            left: 35px;
        }
    }
    
    .b-partnerships__create-your {
        grid-area: create-your;
        @include respond-to(1200) {
            position: relative;
            top: 72px;
            left: 189px;
        }
    }
}
