.hexagon {
    position: relative;
    width: 10px; 
    height: 5.77px;
    background-color: #ffc107;
    margin: 0 8px 3px 0;
    display: inline-block;
    padding-right: 10px;
    @include respond-to(992) {
        padding-right: 0;
    }
}

.hexagon:before,
.hexagon:after {
    content: "";
    position: absolute;
    width: 0;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
}

.hexagon:before {
    bottom: 100%;
    border-bottom: 2.89px solid #ffc107;
}

.hexagon:after {
    top: 100%;
    width: 0;
    border-top: 2.89px solid #ffc107;
}