.b-work {
    padding: 6.5rem 20px;
    @include respond-to(1200) {
        padding: 0 0 8.5rem;
    }
}
.b-work__layout {
    @include respond-to(992) {
        display: grid;
        grid-gap: 30px;
        grid-template-columns: 1fr 1fr;
    }
    @include respond-to(1200) {
        display: grid;
        grid-gap: 60px;
        grid-template-columns: 1fr 1fr 1fr;
        position: relative;
        left: 15px;
    }
}