.b-preview {
	padding: 3rem 20px;
	text-align: center;
	@include respond-to(992) {
		text-align: left;
		padding: 6.4rem 20px 4rem;
	}
}
.b-preview__layout {
	@include respond-to(992) {
		display: grid;
		grid-template-columns: 1fr 2fr;
		max-width: 1100px;
	}
}
.b-preview__right {
	@include respond-to(992) {
		padding-left: 2rem;
		display: grid;
		grid-template-areas: 
			"big-image big-image"
			". .";
	}
	@include respond-to(1250) {
		padding-left: 0;
		margin-left: 20px;
	}
	
	& img:nth-child(2) {
		@include respond-to(1250) {
			margin-left: 9px;
		}
	}
	& img:nth-child(3) {
		@include respond-to(1250) {
			margin-left: auto;
		}
	}
	
	.b-preview__right-img--small {
		@include respond-to(992) {
			padding: 0 1rem;
		}
		@include respond-to(1250) {
			padding: 0;
		}
	}
}

.b-preview__intro-title {
	margin-bottom: 1.2rem;
	line-height: 44px;
	@include respond-to(992) {
		max-width: 340px;
	}
}

.b-preview__intro__text {
	margin-bottom: 2rem;
	font-size: 14px;
	line-height: 20px;
	@include respond-to(992) {
		max-width: 340px;
	}
}

.b-preview__intro__img,
.b-preview__right-img {
	margin:0 auto 2rem;
	border-radius: 6px;
	@include respond-to(992) {
		margin:0 0 2rem;
	}
}

.b-preview__intro__img {
	@include respond-to(992) {
		margin-top: 3.8rem;
	}
}

.b-preview__right-img {
	@include respond-to(992) {
		margin:0 auto 3.5rem;
	}
	@include respond-to(1200) {
		margin:0 0 3.5rem;
	}
}

.b-preview__right-img--big {
	display: none;
	@include respond-to(992) {
		display: block;
		width: 100%;
	}
}

.b-preview__right-img--big {
	@include respond-to(992) {
		grid-area: big-image;
	}
}

