/*
Layout Rules: Dictate major layout components.

Layout Rules divide the page into sections and hold one or more modules
together. Wrappers, rows, columns and any grid-based styles can go here.
Also major semantic areas as well, such as header, content and footer.
*/

/**
* -----------------------------------------------------------------------
*                                  Colors
* -----------------------------------------------------------------------
*/

.c-white { color: $c-white }
.c-black { color: $c-black }

/**
* -----------------------------------------------------------------------
*                                Background
* -----------------------------------------------------------------------
*/

.bg-white { background-color: $c-white }
.bg-black { background-color: $c-black }
.bg-gray { background-color: $gainsboro }

/**
* -----------------------------------------------------------------------
*                              Layout Parts
* -----------------------------------------------------------------------
*/

body{
    // lost-utility: edit;
    
}

.l-list-reset {
    list-style: none;
    padding-left: 0;
}

.l-clearfix {
    &::after {
        clear: both;
    }

    &::after,&::before {
        content: " ";
        display: table;
    }
}
.l-link {
    color: $c-green;
    font-family: $roboto;
    text-transform: uppercase;
}

.l-visibility-hidden {
    position: absolute;
    top: -9999px;
    left: -9999px;
}

.img-responsive {
    display: block;
    height: auto;
    max-width: 100%;
}

.h1 {
    font-range: 320px 1920px;
    font-size: responsive 28px 64px;
    color: $c-mine-shaft;
    font-weight: 900;
}

.h2 {
    font-range: 320px 1920px;
    font-size: responsive 30px 36px;
    color: #323231;
    font-family: $roboto;
    font-weight: 900;
}

.h3 {
    font-range: 320px 1920px;
    font-size: responsive 35px 45px;
}

.h4 {
    font-range: 320px 1920px;
    font-size: responsive 30px 40px;
}

.h5 {
    font-range: 320px 1920px;
    font-size: responsive 20px 27px;
}

.l-container {
    max-width: 1140px;
    margin: 0 auto;
    width: 100%;
}

.l-text-center {
    text-align: center;
}

#mask {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.5;
  /* background: url('../images/overlay.png') no-repeat center; */
  background-blend-mode: saturation;
  background-image: linear-gradient(#000, #000), url('../img/overlay/Our results/ Single V2.png');
  // background-size: auto 100%;
  width: 1336px;
  height: 9575px;
  z-index: 9999;
  // border: 1px solid red;
  box-sizing: content-box;
  transition: all 0.3s ease-in;
  display: none;
}


.swiper-pagination-progressbar.l-swiper-pagination {
    margin-top: 10px;
    position: relative;
}

.l-btn {
    border-radius: 29px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    display: inline-block;
    font-family: $roboto;
    font-size: 12px;
    font-weight: 500;
    padding: 4px 26px;
    text-transform: uppercase;
    &--green {
        background-color: $c-green; 
        color: $c-white;
    }
    &--next-arrow {
        position: relative;
        left: 2px;
        top: -1px;
        @include respond-to(992) {
            top: 0;
        }
    }
}
