.m-hamburger {
  height: 60px;
  left: 0px;
  transition-duration: 0.5s;
  width: 60px;
  position: relative;
  transform: scale(0.6);
  z-index: 99; }
  .m-hamburger::hover {
    cursor: pointer; }
  @media screen and (min-width: 992px) {
