      display: none; } }
  .m-hamburger .icon-left {
    background-color: #212121;
    height: 8px;
    left: 0px;
    position: absolute;
    top: 30px;
    transition-duration: 0.5s;
    width: 30px; }
    .m-hamburger .icon-left::before {
      background-color: #212121;
      content: "";
      height: 8px;
      position: absolute;
      top: -20px;
      transition-duration: 0.5s;
      width: 30px; }
    .m-hamburger .icon-left::after {
      background-color: #212121;
      content: "";
      height: 8px;
      position: absolute;
      top: 20px;
      transition-duration: 0.5s;
      width: 30px; }
    .m-hamburger .icon-left::hover {
      cursor: pointer; }
  .m-hamburger .icon-right {
    background-color: #212121;
    height: 8px;
    left: 30px;
    position: absolute;
    top: 30px;
    transition-duration: 0.5s;
    width: 30px; }
    .m-hamburger .icon-right::before {
      background-color: #212121;
      content: "";
      height: 8px;
      position: absolute;
      top: -20px;
      transition-duration: 0.5s;
      width: 30px; }
    .m-hamburger .icon-right::after {
      background-color: #212121;
      content: "";
      height: 8px;
      position: absolute;
      top: 20px;
      transition-duration: 0.5s;
      width: 30px; }
  .m-hamburger.open .icon-left {
    background: transparent;
    transition-duration: 0.5s; }
    .m-hamburger.open .icon-left::before {
      transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px); }
    .m-hamburger.open .icon-left::after {
      transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px); }
  .m-hamburger.open .icon-right {
    transition-duration: 0.5s;
    background: transparent; }
    .m-hamburger.open .icon-right::before {
      transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px); }
    .m-hamburger.open .icon-right::after {
      -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
      transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px); }

