.b-help {
    padding: 3rem 1.3rem;
    @include respond-to(992) {
        padding: 2.2rem;
    }
    @include respond-to(1400) {
        padding: 1.9rem 0 1.7rem;
    }
    &--about-us {
        @include respond-to(1400) {
            padding: 4.1rem 1.7rem;
        }
        .m-box--custom {
            @include respond-to(1200) {
                max-height: none;
            }
        }
        .m-box--marketing {
            @include respond-to(1200) {
                position: relative;
                top: 137px;
            }
        }
    }
    .b-help__intro-title {
        line-height: 44px;
    }
}

.b-help__layout {
    display: grid;
    grid-gap: 60px;
    @include respond-to(992) {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
        "a a"
        "mobile marketing"; 
    }
    
    @include respond-to(1200) {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas: 
        "a a a"
        ". mobile marketing";
        position: relative;
        left: 16px;
    }
    
    .b-help__intro {
        @include respond-to(992) {
            grid-area: a;
        }
        @include respond-to(992) {
            grid-area: initial;
        }
    }
    
    .m-box {
        @include respond-to(1200) {
            &.m-box--mobile {
                grid-area: mobile;
            }
            &.m-box--marketing {
                grid-area: marketing;
            }
        }
    }
    
    .m-box--custom {
        @include respond-to(1200) {
            position: relative;
            top: 136px;
            max-height: 255px;
        }
    }
    .m-box--marketing {
        @include respond-to(1200) {
            position: relative;
            top: 63px;
        }
    }
}

.b-help__intro-title {
    margin-top: 10px;
}

.b-help__intro-body {
    font-size: 14px;
    line-height: 19px;
    margin-top: 17px;
    margin-bottom: 30px;
}